  <div class="mangban">
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      left-arrow
      @click-left="pageBack"
      @click-right="openAction"
    />
    <div class="cell_group">
      <!--  -->
      <cell-value title="" :value="$userInfo.officeName" disable></cell-value>
      <!--  -->
      <cell-value title="" :value="$userInfo.userName" disable></cell-value>
      <!--  -->
      <cell-value title="" value disable></cell-value>
      <!--  -->
      <cell-value title="" value="" disable></cell-value>
      <!--  -->
      <cell-select-tag
        required
        title=""
        storeKey="reason"
        :tagList="reason"
        :showList="list_1"
        :storeModule="storeModule"
      ></cell-select-tag>
      <!--  -->
      <cell-select-tag
        required
        title=""
        storeKey="endangerSign"
        :tagList="endangerSign"
        :showList="list_2"
        :storeModule="storeModule"
      ></cell-select-tag>
      <!--  -->
      <cell-time v-model="sendData.offtimeStart" title="" required></cell-time>
      <!--  -->
      <cell-time v-model="sendData.powertimeEnd" title="" required></cell-time>
      <!--  -->
      <div class="cell">
        <div class="cell_title">
          <span></span>
        </div>
        <div class="cell_value">
          <span>12</span>
          <span class="cell_value_arrow">
            <van-icon name="search" />
          </span>
        </div>
      </div>
      <!--  -->
      <cell-select-user
        title=""
        required
        :storeModule="storeModule"
        storeKey="workCharger"
        v-model="sendData.workCharger"
      ></cell-select-user>
      <!--  -->
      <div class="cell">
        <div class="cell_title">
          <span></span>
        </div>
        <div class="cell_value">
          <span>12</span>
          <span class="cell_value_arrow">
            <van-icon name="search" />
          </span>
        </div>
      </div>
      <!-- -->
      <cell-textarea v-model="sendData.offExplain" title="" placeholder=""></cell-textarea>
      <div class="cell border_none">
        <div class="cell_other">
          <div class="upload">
            <div class="upload_icon">
              <van-icon name="photo-o" />
            </div>
            <div class="upload_box">
              <van-uploader
                :before-read="beforeRead"
                :before-delete="beforeDelete"
                v-model="fileList"
                preview-size="5rem"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="confirm">
      <div class="head">
        <div class="head_1"></div>
        <div class="head_2"></div>
        <div class="head_3"></div>
      </div>
      <div class="confirm_list">
        <Signature
          :checked="checked[0] ? checked[0].checked : false"
          :img="checked[0] ? checked[0].img : ''"
          @checked="showSignature(0)"
          @cancel="signatureCancel(0)"
        >
          <div slot>,(),</div>
        </Signature>
        <Signature
          :checked="checked[1] ? checked[1].checked : false"
          :img="checked[1] ? checked[1].img : ''"
          @checked="showSignature(1)"
          @cancel="signatureCancel(1)"
        >
          <div slot>,,</div>
        </Signature>
        <Signature
          :checked="checked[2] ? checked[2].checked : false"
          :img="checked[2] ? checked[2].img : ''"
          @checked="showSignature(2)"
          @cancel="signatureCancel(2)"
        >
          <div slot></div>
        </Signature>
      </div>
	  
	  <!--  -->
	  <cell-textarea title="" required placeholder=""></cell-textarea>
	  
	  <!--  -->
	  <cell-input title="" required placeholder=""></cell-input>
	  
    </div>
    <!-- Popup -->
    <van-popup
      class="popup"
      v-model="signatureShow"
      :close-on-click-overlay="false"
      position="bottom"
    >
      <Canvas ref="signature" @save="saveCanvas" @cancel="cancelCanvas"></Canvas>
    </van-popup>
    <!-- Popup -->
    <van-popup v-model="isShowAction" position="bottom" class="action">
      <button @click="postData"></button>
      <button></button>
      <button @click="closeAction"></button>
    </van-popup>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { business } from "@/mixin/business";
import { uploadFile } from "@/mixin/uploadFile";
import Canvas from "@/components/Canvas.vue";
import Signature from "../components/Signature.vue";
export default {
  name: "duanlu",
  mixins: [business, uploadFile],
  components: {
    Signature,
    Canvas
  },
  data() {
    return {
      storeModule: "duanlu",
      sendData: {
        reason: [], //
        endangerSign: [], //
        offtimeStart: "", //
        powertimeEnd: "", //
        workCharger: [], //
        offExplain: "" //
      },
      fileList: [], // 
      list_1: [
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      list_2: ["", "", ""],
      isShowAction: false,
      signatureShow: false,
      checked: [{ checked: false, image: "" }]
    };
  },
  computed: mapState({
    reason: state => state.duanlu.reason,
    endangerSign: state => state.duanlu.endangerSign,
    workCharger: state => state.duanlu.workCharger
  }),
  beforeDestroy() {
    this.$store.dispatch("duanlu/cleanState");
  },
  methods: {
    // Popup
    openAction() {
      this.isShowAction = true;
    },
    // Popup
    closeAction() {
      this.isShowAction = false;
    },
    // 
    postData(again = false, zypId) {
      const that = this;
      let sendData = JSON.parse(JSON.stringify(this.sendData));
      let offExplain = this.fileList.map(item => {
        return item.id;
      });
      sendData.reason = this.stringData("reason", "list_1");
      sendData.endangerSign = this.stringData("endangerSign", "list_2");
      sendData.applyDept = this.$userInfo.officeName;
      sendData.applyer = this.$userInfo.userName;
      sendData.offExplain = offExplain.join(",");
      sendData.__sid = this.$userInfo.sessionId;
      let List = [
        {
          num: 1,
          safetyCs: `,(),`,
          affirmRen: this.checked[0] ? this.checked[0].img : 0,
          safetyStatus: this.checked[0] ? 1 : 0
        },
        {
          num: 2,
          safetyCs: `,,`,
          affirmRen: this.checked[1] ? this.checked[1].img : 0,
          safetyStatus: this.checked[1] ? 1 : 0
        },
        {
          num: 3,
          safetyCs: ``,
          affirmRen: this.checked[2] ? this.checked[2].img : 0,
          safetyStatus: this.checked[2] ? 1 : 0
        }
      ];
      if (again) {
        List.map(item => {
          item.zypId = zypId;
        });
        sendData.htHseDlzypSaftyList = List;
      }
      console.log("sendData: ", sendData);
      this.$api.page_3
        .htHseDlzypSave(sendData)
        .then(res => {
          console.log("res: ", res);
          if (again) {
            this.$Toast.success({
              message: "",
              onClose() {
                that.pageBack();
              }
            });
          } else {
            // 
            this.postData(true, res.message);
          }
        })
        .catch(() => {});
    },
    // 
    showSignature(index) {
      console.log("index: ", index);
      console.log("");
      this.selectSignatureShow = index;
      this.signatureShow = true;
    },
    // 
    signatureCancel(index) {
      console.log("index: ", index);
      console.log("");
      this.checked[index].checked = false;
      this.checked[index].img = "";
    },
    // 
    saveCanvas(e) {
      this.signatureShow = false;
      this.checked[this.selectSignatureShow] = {
        checked: false,
        img: ""
      };
      this.checked[this.selectSignatureShow].img = e;
      console.log("signatureShow: ");
    },
    // 
    cancelCanvas() {
      this.checked[this.selectSignatureShow].checked = false;
      this.checked[this.selectSignatureShow].img = "";
      this.signatureShow = false;
    }
  },
  watch: {
    reason(res) {
      this.sendData.reason = res;
    },
    endangerSign(res) {
      this.sendData.endangerSign = res;
    },
    workCharger(res) {
      this.sendData.workCharger = res;
    }
  }
};
</script>
